import React from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import logo from "../photo/photo.png";
import { Appbar } from "../components/appBar";
import { Sckills } from "../components/skills";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import Button from "@mui/material/Button";
import Tooltip from "@mui/material/Tooltip";

const theme = createTheme({
  typography: {
    fontFamily: "Nunito",
  },
  palette: {
    primary: {
      main: "#f96d00",
    },
  },
});

export class LogIn extends React.Component {
  state = {
    verSkills: false,
  };

  goToGit() {
    window.location.href = "https://github.com/av-edson";
  }
  goToLink() {
    window.location.href = "https://www.linkedin.com/in/edson-avila-725227212/";
  }

  getSkills() {
    return (
      <div data-aos="zoom-in">
        <Sckills />
      </div>
    );
  }
  render() {
    return (
      <ThemeProvider theme={theme}>
        <Appbar />
        <Grid
          container
          component="main"
          sx={{ marginTop: "0px", backgroundColor: "black" }}
        >
          <CssBaseline />

          <Grid
            item
            xs={12}
            sm={6}
            md={6}
            component={Paper}
            elevation={6}
            square
            sx={{ backgroundColor: "black" }}
          >
            <Box
              sx={{
                my: 8,
                mx: 4,
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                backgroundColor: "primary",
                color: "white",
                marginTop: "100px",
              }}
            >
              <Card
                sx={{
                  maxWidth: "100%",
                  // backgroundColor: "#1C0A00",
                  backgroundColor: "black",
                  color: "white",
                }}
              >
                <CardContent>
                  <Typography
                    gutterBottom
                    variant="h2"
                    component="div"
                    className="texto"
                  >
                    Hi!
                  </Typography>
                  <Typography
                    gutterBottom
                    variant="h2"
                    component="div"
                    className="texto"
                  >
                    I 'm Edson
                  </Typography>
                  <Typography variant="h6">
                    I’m full stack developer with experience in building web
                    aplications, variuos programing lenguajes and development
                    tols
                  </Typography>
                  <Typography variant="h6" sx={{ marginTop: "5px" }}>
                    I am currently studying software engineering
                  </Typography>
                  <Grid
                    container
                    rowSpacing={1}
                    columnSpacing={{ xs: 1, sm: 2, md: 3 }}
                    sx={{ marginTop: "20px" }}
                    justifyContent="flex-start"
                  >
                    <Grid item xs={6} sx={{ marginTop: "15px" }}>
                      <Typography variant="h6">My GitHub</Typography>
                    </Grid>
                    <Grid item>
                      <Tooltip
                        TransitionProps={{ timeout: 600 }}
                        title="Go to GitHub"
                      >
                        <Button onClick={() => this.goToGit()}>
                          <GitHubIcon color="primary" sx={{ fontSize: 50 }} />
                        </Button>
                      </Tooltip>
                    </Grid>
                  </Grid>
                  <Grid
                    container
                    rowSpacing={1}
                    columnSpacing={{ xs: 1, sm: 2, md: 3 }}
                    sx={{ marginTop: "30px" }}
                    justifyContent="flex-start"
                  >
                    <Grid item xs={6} sx={{ marginTop: "15px" }}>
                      <Typography variant="h6">My Linkedin</Typography>
                    </Grid>
                    <Grid item>
                      <Tooltip
                        TransitionProps={{ timeout: 600 }}
                        title="Go to Linkedin"
                      >
                        <Button onClick={() => this.goToLink()}>
                          <LinkedInIcon color="primary" sx={{ fontSize: 50 }} />
                        </Button>
                      </Tooltip>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Box>
          </Grid>

          <Grid
            className="ocultar"
            item
            sm={6}
            md={6}
            sx={{
              backgroundRepeat: "no-repeat",
              backgroundColor: "black",
              backgroundSize: "cover",
              backgroundPosition: "center",
            }}
          >
            <img
              alt=""
              src={logo}
              style={{
                filter: "grayscale(30%)",
                marginTop: "50px",
                marginLeft: "10%",
              }}
              width="90%"
            />
          </Grid>
        </Grid>
        <Grid
          container
          justifyContent="center"
          sx={{ backgroundColor: "#010407" }}
        >
          <Grid item sx={{ width: "100%" }}>
            {this.getSkills()}
          </Grid>
        </Grid>
      </ThemeProvider>
    );
  }
}
